.exhibitionBox a img {
  width: 100%;
}
.exhibitionBox a .flexBox {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 12px 5px;
}
.exhibitionBox a .flexBox .title {
  font-family: var(--font-EB);
  font-size: 20px;
}
.exhibitionBox a .flexBox .stateBtn {
  font-size: 12px;
  border: 1px solid #222;
  border-radius: 30px;
  width: max-content;
  padding: 5px 10px;
  cursor: default;
}
.exhibitionBox a .location {
  font-size: 14px;
  text-align: left;
  padding: 6px 5px;
}
.exhibitionBox a .date {
  font-size: 14px;
  text-align: left;
  padding-left: 5px;
}